import React, { useState, useEffect, useRef } from 'react';
import {
  Typography, Box, Button, Paper, 
  Grid, FormControl, InputLabel, Select, MenuItem,
  CircularProgress, Alert, IconButton, Slider,
  TextField, Divider, Chip, Tooltip
} from '@mui/material';
import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline';
import StopIcon from '@mui/icons-material/Stop';
import DownloadIcon from '@mui/icons-material/Download';
import InfoIcon from '@mui/icons-material/Info';
import { VoiceAPI } from '../../services/api';
import TtsHistory from './TtsHistory';

const TextToSpeechTab = () => {
  // State variables
  const [text, setText] = useState('');
  const [availableSpeakers, setAvailableSpeakers] = useState([]);
  const [selectedSpeaker, setSelectedSpeaker] = useState('');
  const [language, setLanguage] = useState('english');
  const [speed, setSpeed] = useState(1.0);
  const [isLoading, setIsLoading] = useState(false);
  const [result, setResult] = useState(null);
  const [error, setError] = useState('');
  const [isPlaying, setIsPlaying] = useState(false);
  const [showHistory, setShowHistory] = useState(false);
  const [showInfo, setShowInfo] = useState(false);
  
  // Tham chiu n i tng Audio
  const audioRef = useRef(null);

  // Ti danh sch speakers t backend
  useEffect(() => {
    const fetchSpeakers = async () => {
      try {
        const response = await VoiceAPI.getTtsSpeakers();
        if (response.data.speakers && response.data.speakers.length > 0) {
          setAvailableSpeakers(response.data.speakers);
          setSelectedSpeaker(response.data.speakers[0]);
        } else {
          setError('Khng tm thy speakers no');
          setShowInfo(true);
        }
      } catch (err) {
        console.error('Li khi ti danh sch speakers:', err);
        setError('Khng th kt ni vi my ch. Vui lng th li sau.');
        setShowInfo(true);
      }
    };

    fetchSpeakers();
  }, []);

  // X l khi ngi dng thay i text
  const handleTextChange = (event) => {
    setText(event.target.value);
  };

  // X l khi ngi dng thay i speaker
  const handleSpeakerChange = (event) => {
    setSelectedSpeaker(event.target.value);
    
    // T ng chn ngn ng da trn speaker
    const speaker = event.target.value;
    if (speaker.startsWith('en-')) {
      setLanguage('english');
    } else if (speaker === 'zh') {
      setLanguage('chinese');
    } else if (speaker === 'fr') {
      setLanguage('french');
    } else if (speaker === 'es') {
      setLanguage('spanish');
    } else if (speaker === 'jp') {
      setLanguage('japanese');
    } else if (speaker === 'kr') {
      setLanguage('korean');
    }
  };

  // X l khi ngi dng thay i ngn ng
  const handleLanguageChange = (event) => {
    setLanguage(event.target.value);
  };

  // X l khi ngi dng thay i tc 
  const handleSpeedChange = (event, newValue) => {
    setSpeed(newValue);
  };

  // X l khi ngi dng gi yu cu to ging ni
  const handleGenerateTts = async () => {
    if (!text) {
      setError('Vui lng nhp vn bn');
      return;
    }

    if (!selectedSpeaker) {
      setError('Vui lng chn speaker');
      return;
    }

    setIsLoading(true);
    setResult(null);
    setError('');

    try {
      const response = await VoiceAPI.generateTts({
        text,
        speaker: selectedSpeaker,
        language,
        speed
      });

      if (response.data.success) {
        setResult(response.data);
        setError('');
      } else {
        setError(response.data.error || 'Li khng xc nh');
        if (response.data.error && response.data.error.includes('MeloTTS')) {
          setShowInfo(true);
        }
      }
    } catch (err) {
      console.error('Li khi to ging ni:', err);
      setError(err.response?.data?.error || 'Khng th kt ni vi my ch');
      if (err.response?.data?.error && err.response.data.error.includes('MeloTTS')) {
        setShowInfo(true);
      }
    } finally {
      setIsLoading(false);
    }
  };

  // X l pht audio
  const handlePlayAudio = () => {
    if (!result) return;

    if (isPlaying) {
      audioRef.current.pause();
      audioRef.current.currentTime = 0;
      setIsPlaying(false);
    } else {
      // Lun to i tng Audio mi vi URL hin ti
      audioRef.current = new Audio(result.result_url);
      audioRef.current.onended = () => setIsPlaying(false);
      
      // Cp nht tc  pht nu cn
      audioRef.current.playbackRate = speed;

      audioRef.current.play()
        .then(() => setIsPlaying(true))
        .catch(err => {
          console.error('Li khi pht audio:', err);
          setError('Khng th pht audio');
        });
    }
  };

  // Cp nht audioRef khi result thay i
  useEffect(() => {
    // Dn dp audio trc  nu c
    if (audioRef.current) {
      audioRef.current.pause();
      audioRef.current = null;
    }
  }, [result]);

  // X l ti xung kt qu
  const handleDownload = () => {
    if (result && result.result_url) {
      VoiceAPI.downloadResult(result.result_url);
    }
  };

  // Cleanup khi component unmount
  useEffect(() => {
    return () => {
      if (audioRef.current) {
        audioRef.current.pause();
        audioRef.current = null;
      }
    };
  }, []);

  const toggleHistory = () => {
    setShowHistory(!showHistory);
  };
  
  const toggleInfo = () => {
    setShowInfo(!showInfo);
  };
  
  // nh x tn ngn ng  hin th
  const getLanguageDisplayName = (code) => {
    const languageMap = {
      'english': 'Ting Anh',
      'chinese': 'Ting Trung',
      'french': 'Ting Php',
      'spanish': 'Ting Ty Ban Nha',
      'japanese': 'Ting Nht',
      'korean': 'Ting Hn'
    };
    return languageMap[code] || code;
  };
  
  // nh x tn speaker  hin th d c
  const getSpeakerDisplayName = (speakerCode) => {
    const speakerMap = {
      'en-us': 'Ting Anh - M',
      'en-br': 'Ting Anh - Anh',
      'en-au': 'Ting Anh - c',
      'en-india': 'Ting Anh - n ',
      'en-default': 'Ting Anh - Mc nh',
      'en-newest': 'Ting Anh - Mi nht',
      'zh': 'Ting Trung',
      'jp': 'Ting Nht',
      'kr': 'Ting Hn',
      'fr': 'Ting Php',
      'es': 'Ting Ty Ban Nha'
    };
    return speakerMap[speakerCode] || speakerCode;
  };

  return (
    <Box>
      <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
          <Typography variant="h6">
            Chuyn vn bn thnh ging ni
          </Typography>
          <Tooltip title="Xem thng tin v tnh nng TTS">
            <IconButton onClick={toggleInfo}>
              <InfoIcon />
            </IconButton>
          </Tooltip>
        </Box>
        
        {showInfo && (
          <Alert severity="info" sx={{ mb: 3 }}>
            <Typography variant="body2" gutterBottom>
              Tnh nng ny s dng MeloTTS v OpenVoice  to ging ni t vn bn:
            </Typography>
            <ul style={{ margin: '8px 0', paddingLeft: '20px' }}>
              <li>H tr nhiu ngn ng: Anh, Trung, Php, Ty Ban Nha, Nht, Hn</li>
              <li>Mi language code (en-us, zh, v.v.) tng ng vi mt accent khc nhau</li>
              <li>Ln u s dng, h thng s t ng ci t MeloTTS (mt khong 1-2 pht), vui lng i v th li</li>
              <li>Chn speaker ph hp vi ngn ng ca vn bn  c kt qu tt nht</li>
            </ul>
          </Alert>
        )}
        
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <TextField
              label="Vn bn cn chuyn thnh ging ni"
              multiline
              rows={4}
              fullWidth
              value={text}
              onChange={handleTextChange}
              variant="outlined"
              placeholder="Nhp vn bn  y..."
            />
          </Grid>
          
          <Grid item xs={12} md={6}>
            <FormControl fullWidth>
              <InputLabel id="speaker-select-label">Speaker</InputLabel>
              <Select
                labelId="speaker-select-label"
                id="speaker-select"
                value={selectedSpeaker}
                label="Speaker"
                onChange={handleSpeakerChange}
                disabled={isLoading || availableSpeakers.length === 0}
              >
                {availableSpeakers.map((speaker) => (
                  <MenuItem key={speaker} value={speaker}>
                    {getSpeakerDisplayName(speaker)}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
          
          <Grid item xs={12} md={6}>
            <FormControl fullWidth>
              <InputLabel id="language-select-label">Ngn ng</InputLabel>
              <Select
                labelId="language-select-label"
                id="language-select"
                value={language}
                label="Ngn ng"
                onChange={handleLanguageChange}
                disabled={isLoading}
              >
                <MenuItem value="english">{getLanguageDisplayName('english')}</MenuItem>
                <MenuItem value="chinese">{getLanguageDisplayName('chinese')}</MenuItem>
                <MenuItem value="french">{getLanguageDisplayName('french')}</MenuItem>
                <MenuItem value="spanish">{getLanguageDisplayName('spanish')}</MenuItem>
                <MenuItem value="japanese">{getLanguageDisplayName('japanese')}</MenuItem>
                <MenuItem value="korean">{getLanguageDisplayName('korean')}</MenuItem>
              </Select>
            </FormControl>
          </Grid>
          
          <Grid item xs={12}>
            <Typography id="speed-slider" gutterBottom>
              Tc : {speed.toFixed(1)}x
            </Typography>
            <Slider
              aria-labelledby="speed-slider"
              value={speed}
              onChange={handleSpeedChange}
              min={0.5}
              max={2.0}
              step={0.1}
              marks={[
                { value: 0.5, label: '0.5x' },
                { value: 1.0, label: '1.0x' },
                { value: 1.5, label: '1.5x' },
                { value: 2.0, label: '2.0x' },
              ]}
              disabled={isLoading}
            />
          </Grid>
          
          <Grid item xs={12}>
            <Box sx={{ display: 'flex', justifyContent: 'center', mt: 2 }}>
              <Button
                variant="contained"
                color="primary"
                onClick={handleGenerateTts}
                disabled={isLoading || !text}
                sx={{ mr: 2 }}
              >
                {isLoading ? (
                  <>
                    <CircularProgress size={24} sx={{ mr: 1 }} />
                    ang x l...
                  </>
                ) : (
                  'To ging ni'
                )}
              </Button>
              
              <Button
                variant="outlined"
                onClick={toggleHistory}
              >
                {showHistory ? 'n lch s' : 'Hin lch s'}
              </Button>
            </Box>
          </Grid>
        </Grid>
        
        {error && (
          <Alert severity="error" sx={{ mt: 2 }}>
            {error}
          </Alert>
        )}
        
        {result && (
          <Box sx={{ mt: 3, p: 2, bgcolor: 'background.paper', borderRadius: 1 }}>
            <Typography variant="subtitle1" gutterBottom>
              Kt qu
            </Typography>
            <Divider sx={{ mb: 2 }} />
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
              <IconButton 
                color="primary" 
                onClick={handlePlayAudio}
                aria-label={isPlaying ? 'Dng pht' : 'Pht audio'}
              >
                {isPlaying ? <StopIcon /> : <PlayCircleOutlineIcon />}
              </IconButton>
              <Typography variant="body1" sx={{ flex: 1, ml: 1 }}>
                {isPlaying ? 'ang pht...' : 'Sn sng pht'}
              </Typography>
              <IconButton 
                color="primary" 
                onClick={handleDownload}
                aria-label="Ti xung"
              >
                <DownloadIcon />
              </IconButton>
            </Box>
            <Box sx={{ mt: 1 }}>
              <Chip label={`Speaker: ${getSpeakerDisplayName(result.speaker)}`} sx={{ mr: 1 }} />
              <Chip label={`Ngn ng: ${getLanguageDisplayName(result.language)}`} sx={{ mr: 1 }} />
              <Chip label={`Tc : ${result.speed}x`} />
            </Box>
          </Box>
        )}
      </Paper>
      
      {showHistory && (
        <Paper elevation={3} sx={{ p: 3 }}>
          <Typography variant="h6" gutterBottom>
            Lch s chuyn i
          </Typography>
          <TtsHistory />
        </Paper>
      )}
    </Box>
  );
};

export default TextToSpeechTab; 