import React, { useState, useEffect, useRef } from 'react';
import { 
  Typography, Box, Button, Paper, 
  Grid, FormControl, InputLabel, Select, MenuItem,
  CircularProgress, Alert, IconButton, Slider, Card,
  CardContent, TextField, Divider, Tooltip, Tabs, Tab
} from '@mui/material';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import AudioFileIcon from '@mui/icons-material/AudioFile';
import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline';
import VolumeUpIcon from '@mui/icons-material/VolumeUp';
import StopIcon from '@mui/icons-material/Stop';
import DownloadIcon from '@mui/icons-material/Download';
import TuneIcon from '@mui/icons-material/Tune';
import MusicNoteIcon from '@mui/icons-material/MusicNote';
import SettingsVoiceIcon from '@mui/icons-material/SettingsVoice';
import BuildIcon from '@mui/icons-material/Build';
import GraphicEqIcon from '@mui/icons-material/GraphicEq';
import FolderZipIcon from '@mui/icons-material/FolderZip';
import { VoiceAPI } from '../../services/api';

// TabPanel component  hin th ni dung tab
function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`rvc-tabpanel-${index}`}
      aria-labelledby={`rvc-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          {children}
        </Box>
      )}
    </div>
  );
}

const RVCTab = () => {
  // State cho tab navigation
  const [activeTab, setActiveTab] = useState(0);

  // State cho file audio input
  const [audioFile, setAudioFile] = useState(null);
  const [audioPreviewUrl, setAudioPreviewUrl] = useState(null);
  const [isSourcePlaying, setIsSourcePlaying] = useState(false);
  const [isResultPlaying, setIsResultPlaying] = useState(false);
  
  // State cho model RVC
  const [availableVoices, setAvailableVoices] = useState([]);
  const [selectedVoice, setSelectedVoice] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [modelLoading, setModelLoading] = useState(true);
  
  // State cho kt qu
  const [result, setResult] = useState(null);
  const [error, setError] = useState('');
  
  // State cho tham s RVC
  const [f0UpKey, setF0UpKey] = useState(0); // iu chnh pitch
  const [indexRate, setIndexRate] = useState(0.5); // T l p dng index feature
  const [protectValue, setProtectValue] = useState(0.33); // Bo v m ni
  const [rmsRate, setRmsRate] = useState(0.25); // T l RMS
  const [showAdvanced, setShowAdvanced] = useState(false);
  
  // State cho UVR5 (Tch ging ni)
  const [uvrFile, setUvrFile] = useState(null);
  const [uvrResult, setUvrResult] = useState(null);
  const [vocalType, setVocalType] = useState('both');
  const [isUvrLoading, setIsUvrLoading] = useState(false);
  const [isVocalsPlaying, setIsVocalsPlaying] = useState(false);
  const [isInstrumentalPlaying, setIsInstrumentalPlaying] = useState(false);
  
  // State cho F0 extraction
  const [f0File, setF0File] = useState(null);
  const [f0Method, setF0Method] = useState('dio');
  const [f0Result, setF0Result] = useState(null);
  const [isF0Loading, setIsF0Loading] = useState(false);
  
  // State cho train model
  const [datasetFile, setDatasetFile] = useState(null);
  const [modelName, setModelName] = useState('');
  const [epochs, setEpochs] = useState(200);
  const [batchSize, setBatchSize] = useState(8);
  const [trainF0Method, setTrainF0Method] = useState('dio');
  const [isTraining, setIsTraining] = useState(false);
  const [trainResult, setTrainResult] = useState(null);
  
  // Refs cho audio players
  const sourceAudioRef = useRef(null);
  const resultAudioRef = useRef(null);
  const vocalsAudioRef = useRef(null);
  const instrumentalAudioRef = useRef(null);

  // Ti danh sch ging ni RVC t backend
  useEffect(() => {
    const fetchVoices = async () => {
      try {
        const response = await VoiceAPI.getModels();
        console.log('Kt qu API models:', response.data);
        
        if (response.data && response.data.rvc) {
          const rvcVoices = response.data.rvc;
          console.log(`Tm thy ${rvcVoices.length} m hnh RVC:`, rvcVoices);
          
          if (rvcVoices.length > 0) {
            setAvailableVoices(rvcVoices);
            setSelectedVoice(rvcVoices[0]);
            setError('');
          } else {
            console.warn('Khng tm thy m hnh RVC no');
            setError('Khng tm thy mu ging ni RVC no. Vui lng thm m hnh vo th mc ai/rvc/models.');
          }
        } else {
          console.error('D liu API khng ng nh dng:', response.data);
          setError('nh dng d liu khng ng. Khng tm thy thng tin v m hnh RVC.');
        }
      } catch (err) {
        console.error('Li khi ti danh sch ging ni RVC:', err);
        setError('Khng th kt ni vi my ch. Vui lng th li sau.');
      } finally {
        setModelLoading(false);
      }
    };

    fetchVoices();
  }, []);

  // X l khi ngi dng chn file
  const handleFileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      if (file.type.includes('audio') || file.name.match(/\.(wav|mp3|flac|ogg)$/)) {
        // Nu ang pht m thanh, dng trc khi thay i file
        if (sourceAudioRef.current) {
          sourceAudioRef.current.pause();
          sourceAudioRef.current = null;
          setIsSourcePlaying(false);
        }
        
        // Gii phng URL c nu c
        if (audioPreviewUrl) {
          URL.revokeObjectURL(audioPreviewUrl);
        }
        
        setAudioFile(file);
        setError('');
        
        // To URL cho file audio  nghe th
        const fileUrl = URL.createObjectURL(file);
        setAudioPreviewUrl(fileUrl);
        
        // Reset kt qu khi chn file mi
        setResult(null);
      } else {
        setError('Vui lng chn file m thanh hp l (WAV, MP3, FLAC, OGG)');
        setAudioFile(null);
        setAudioPreviewUrl(null);
      }
    }
  };

  // X l khi ngi dng chn file cho UVR5
  const handleUvrFileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      if (file.type.includes('audio') || file.name.match(/\.(wav|mp3|flac|ogg)$/)) {
        setUvrFile(file);
        setUvrResult(null);
        setError('');
      } else {
        setError('Vui lng chn file m thanh hp l (WAV, MP3, FLAC, OGG)');
        setUvrFile(null);
      }
    }
  };

  // X l khi ngi dng chn ging ni
  const handleVoiceChange = (event) => {
    setSelectedVoice(event.target.value);
  };

  // X l khi ngi dng nhn nt chuyn i
  const handleConvert = async () => {
    if (!audioFile) {
      setError('Vui lng chn file m thanh');
      return;
    }
    
    if (!selectedVoice) {
      setError('Vui lng chn ging ni ch');
      return;
    }
    
    setIsLoading(true);
    setError('');
    setResult(null);
    
    try {
      // To FormData  gi ln server
      const formData = new FormData();
      formData.append('audio', audioFile);
      formData.append('model_type', 'rvc');
      formData.append('target_voice', selectedVoice);
      
      // Thm cc tham s nng cao
      formData.append('f0up_key', f0UpKey.toString());
      formData.append('index_rate', indexRate.toString());
      formData.append('protect', protectValue.toString());
      formData.append('rms_mix_rate', rmsRate.toString());
      
      // Gi request ln server
      const response = await VoiceAPI.convertVoice(formData);
      
      if (response.data.success) {
        setResult(response.data);
        console.log(' nhn kt qu:', response.data);
      } else {
        setError(`Chuyn i tht bi: ${response.data.error || 'Li khng xc nh'}`);
      }
    } catch (err) {
      console.error('Li khi chuyn i ging ni:', err);
      if (err.response && err.response.data && err.response.data.error) {
        setError(`Li: ${err.response.data.error}`);
      } else {
        setError('Li kt ni n my ch. Vui lng th li sau.');
      }
    } finally {
      setIsLoading(false);
    }
  };

  // X l khi ngi dng nhn nt tch ging ni
  const handleSeparateVocals = async () => {
    if (!uvrFile) {
      setError('Vui lng chn file m thanh');
      return;
    }
    
    setIsUvrLoading(true);
    setError('');
    setUvrResult(null);
    
    try {
      // Gi API tch ging ni
      const response = await VoiceAPI.rvc.separateVocals(uvrFile, vocalType);
      
      if (response.data.success) {
        setUvrResult(response.data);
        console.log(' nhn kt qu UVR5:', response.data);
      } else {
        setError(`Tch ging ni tht bi: ${response.data.error || 'Li khng xc nh'}`);
      }
    } catch (err) {
      console.error('Li khi tch ging ni:', err);
      if (err.response && err.response.data && err.response.data.error) {
        setError(`Li: ${err.response.data.error}`);
      } else {
        setError('Li kt ni n my ch. Vui lng th li sau.');
      }
    } finally {
      setIsUvrLoading(false);
    }
  };

  // X l pht/dng m thanh gc
  const handlePlaySourceAudio = () => {
    if (isSourcePlaying) {
      // Nu ang pht th dng li
      if (sourceAudioRef.current) {
        sourceAudioRef.current.pause();
        setIsSourcePlaying(false);
      }
    } else {
      // Nu cha pht th to v pht
      const audio = new Audio(audioPreviewUrl);
      audio.onended = () => {
        setIsSourcePlaying(false);
        sourceAudioRef.current = null;
      };
      
      sourceAudioRef.current = audio;
      audio.play()
        .then(() => setIsSourcePlaying(true))
        .catch(error => {
          console.error('Li khi pht m thanh:', error);
          setError('Khng th pht m thanh. Vui lng th li.');
          setIsSourcePlaying(false);
        });
    }
  };

  // X l pht/dng m thanh kt qu
  const handlePlayResultAudio = () => {
    if (!result || !result.result_url) {
      setError('Khng c kt qu  pht');
      return;
    }
    
    if (isResultPlaying) {
      // Nu ang pht th dng li
      if (resultAudioRef.current) {
        resultAudioRef.current.pause();
        setIsResultPlaying(false);
      }
    } else {
      // Nu cha pht th to v pht
      const audio = new Audio(result.result_url);
      audio.onended = () => {
        setIsResultPlaying(false);
        resultAudioRef.current = null;
      };
      
      resultAudioRef.current = audio;
      audio.play()
        .then(() => setIsResultPlaying(true))
        .catch(error => {
          console.error('Li khi pht m thanh kt qu:', error);
          setError('Khng th pht m thanh kt qu. Vui lng th li.');
          setIsResultPlaying(false);
        });
    }
  };

  // X l pht/dng file vocals
  const handlePlayVocals = () => {
    if (!uvrResult || !uvrResult.vocals_url) {
      setError('Khng c file vocals  pht');
      return;
    }
    
    if (isVocalsPlaying) {
      if (vocalsAudioRef.current) {
        vocalsAudioRef.current.pause();
        setIsVocalsPlaying(false);
      }
    } else {
      const audio = new Audio(uvrResult.vocals_url);
      audio.onended = () => {
        setIsVocalsPlaying(false);
        vocalsAudioRef.current = null;
      };
      
      vocalsAudioRef.current = audio;
      audio.play()
        .then(() => setIsVocalsPlaying(true))
        .catch(error => {
          console.error('Li khi pht file vocals:', error);
          setError('Khng th pht file vocals. Vui lng th li.');
          setIsVocalsPlaying(false);
        });
    }
  };

  // X l pht/dng file instrumental
  const handlePlayInstrumental = () => {
    if (!uvrResult || !uvrResult.instrumental_url) {
      setError('Khng c file instrumental  pht');
      return;
    }
    
    if (isInstrumentalPlaying) {
      if (instrumentalAudioRef.current) {
        instrumentalAudioRef.current.pause();
        setIsInstrumentalPlaying(false);
      }
    } else {
      const audio = new Audio(uvrResult.instrumental_url);
      audio.onended = () => {
        setIsInstrumentalPlaying(false);
        instrumentalAudioRef.current = null;
      };
      
      instrumentalAudioRef.current = audio;
      audio.play()
        .then(() => setIsInstrumentalPlaying(true))
        .catch(error => {
          console.error('Li khi pht file instrumental:', error);
          setError('Khng th pht file instrumental. Vui lng th li.');
          setIsInstrumentalPlaying(false);
        });
    }
  };

  // X l ti xung kt qu
  const handleDownload = (url) => {
    if (url) {
      VoiceAPI.downloadResult(url);
    } else {
      setError('Khng c kt qu  ti xung');
    }
  };

  // X l chuyn i tab
  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };
  
  // X l trch xut F0
  const handleExtractF0 = async () => {
    if (!f0File) {
      setError('Vui lng chn file m thanh');
      return;
    }
    
    setIsF0Loading(true);
    setError('');
    setF0Result(null);
    
    try {
      // Gi API trch xut F0
      const response = await VoiceAPI.rvc.extractF0(f0File, f0Method);
      
      if (response.data.success) {
        setF0Result(response.data);
        console.log(' nhn kt qu F0:', response.data);
      } else {
        setError(`Trch xut F0 tht bi: ${response.data.error || 'Li khng xc nh'}`);
      }
    } catch (err) {
      console.error('Li khi trch xut F0:', err);
      if (err.response && err.response.data && err.response.data.error) {
        setError(`Li: ${err.response.data.error}`);
      } else {
        setError('Li kt ni n my ch. Vui lng th li sau.');
      }
    } finally {
      setIsF0Loading(false);
    }
  };
  
  // X l khi ngi dng chn file cho F0
  const handleF0FileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      if (file.type.includes('audio') || file.name.match(/\.(wav|mp3|flac|ogg)$/)) {
        setF0File(file);
        setF0Result(null);
        setError('');
      } else {
        setError('Vui lng chn file m thanh hp l (WAV, MP3, FLAC, OGG)');
        setF0File(null);
      }
    }
  };
  
  // X l khi ngi dng chn dataset cho train model
  const handleDatasetFileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      if (file.type === 'application/zip' || file.name.endsWith('.zip')) {
        setDatasetFile(file);
        setTrainResult(null);
        setError('');
      } else {
        setError('Vui lng chn file ZIP cha dataset');
        setDatasetFile(null);
      }
    }
  };
  
  // X l train model
  const handleTrainModel = async () => {
    if (!datasetFile) {
      setError('Vui lng chn file dataset');
      return;
    }
    
    if (!modelName) {
      setError('Vui lng nhp tn model');
      return;
    }
    
    setIsTraining(true);
    setError('');
    setTrainResult(null);
    
    try {
      // Gi API train model
      const response = await VoiceAPI.rvc.trainModel(
        datasetFile, 
        modelName,
        epochs,
        batchSize,
        trainF0Method
      );
      
      if (response.data.success) {
        setTrainResult(response.data);
        console.log(' nhn kt qu train model:', response.data);
      } else {
        setError(`Hun luyn model tht bi: ${response.data.error || 'Li khng xc nh'}`);
      }
    } catch (err) {
      console.error('Li khi hun luyn model:', err);
      if (err.response && err.response.data && err.response.data.error) {
        setError(`Li: ${err.response.data.error}`);
      } else {
        setError('Li kt ni n my ch. Vui lng th li sau.');
      }
    } finally {
      setIsTraining(false);
    }
  };

  return (
    <Box sx={{ p: 2 }}>
      <Paper elevation={2} sx={{ p: 3, mb: 3 }}>
        <Tabs 
          value={activeTab} 
          onChange={handleTabChange} 
          aria-label="RVC options tabs"
          variant="fullWidth"
        >
          <Tab icon={<SettingsVoiceIcon />} label="Chuyn i" id="rvc-tab-0" />
          <Tab icon={<MusicNoteIcon />} label="UVR5" id="rvc-tab-1" />
          <Tab icon={<GraphicEqIcon />} label="F0" id="rvc-tab-2" />
          <Tab icon={<BuildIcon />} label="Train Model" id="rvc-tab-3" />
        </Tabs>
        
        {/* Tab 1: Chuyn i ging ni */}
        <TabPanel value={activeTab} index={0}>
          <Typography variant="h6" gutterBottom>
            Chuyn i ging ni vi RVC
          </Typography>
          
          {/* Chn file m thanh */}
          <Grid container spacing={2} sx={{ mt: 1, mb: 2 }}>
            <Grid item xs={12} md={6}>
              <Button
                variant="outlined"
                component="label"
                startIcon={<CloudUploadIcon />}
                fullWidth
                sx={{ height: '56px' }}
              >
                Chn file m thanh
                <input
                  type="file"
                  accept="audio/*"
                  hidden
                  onChange={handleFileChange}
                />
              </Button>
            </Grid>
            <Grid item xs={12} md={6}>
              <Paper variant="outlined" sx={{ p: 1, display: 'flex', alignItems: 'center', height: '56px' }}>
                {audioFile ? (
                  <>
                    <AudioFileIcon color="primary" sx={{ mx: 1 }} />
                    <Typography variant="body2" sx={{ flex: 1, overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                      {audioFile.name}
                    </Typography>
                    <IconButton 
                      onClick={handlePlaySourceAudio}
                      disabled={!audioPreviewUrl}
                      color="primary"
                    >
                      {isSourcePlaying ? <StopIcon /> : <PlayCircleOutlineIcon />}
                    </IconButton>
                  </>
                ) : (
                  <Typography variant="body2" color="textSecondary" sx={{ mx: 1 }}>
                    Cha chn file no
                  </Typography>
                )}
              </Paper>
            </Grid>
          </Grid>
          
          {/* Chn ging ni ch */}
          <FormControl fullWidth sx={{ mb: 2 }}>
            <InputLabel>Ging ni ch</InputLabel>
            <Select
              value={selectedVoice}
              onChange={handleVoiceChange}
              label="Ging ni ch"
              disabled={modelLoading}
            >
              {modelLoading ? (
                <MenuItem value="">ang ti...</MenuItem>
              ) : (
                availableVoices.map((voice) => (
                  <MenuItem key={voice} value={voice}>
                    {voice}
                  </MenuItem>
                ))
              )}
            </Select>
          </FormControl>
          
          {/* iu chnh Pitch (f0up_key) */}
          <Typography gutterBottom>
            iu chnh  cao (Pitch)
          </Typography>
          <Grid container spacing={2} alignItems="center">
            <Grid item xs>
              <Slider
                value={f0UpKey}
                onChange={(e, newValue) => setF0UpKey(newValue)}
                min={-12}
                max={12}
                step={1}
                marks
                valueLabelDisplay="auto"
              />
            </Grid>
            <Grid item>
              <Typography variant="body2">
                {f0UpKey > 0 ? `+${f0UpKey}` : f0UpKey}
              </Typography>
            </Grid>
          </Grid>
          
          {/* Nt hin th tham s nng cao */}
          <Box sx={{ mt: 2, mb: 1 }}>
            <Button 
              startIcon={<TuneIcon />}
              onClick={() => setShowAdvanced(!showAdvanced)}
              size="small"
            >
              {showAdvanced ? 'n tham s nng cao' : 'Hin th tham s nng cao'}
            </Button>
          </Box>
          
          {/* Tham s nng cao */}
          {showAdvanced && (
            <Card variant="outlined" sx={{ mt: 1, mb: 2 }}>
              <CardContent>
                <Typography variant="subtitle2" gutterBottom>
                  Tham s nng cao
                </Typography>
                
                {/* Index Rate */}
                <Typography variant="body2" gutterBottom>
                  T l p dng index (Index Rate)
                </Typography>
                <Grid container spacing={2} alignItems="center">
                  <Grid item xs>
                    <Slider
                      value={indexRate}
                      onChange={(e, newValue) => setIndexRate(newValue)}
                      min={0}
                      max={1}
                      step={0.01}
                      valueLabelDisplay="auto"
                    />
                  </Grid>
                  <Grid item>
                    <Typography variant="body2">
                      {indexRate.toFixed(2)}
                    </Typography>
                  </Grid>
                </Grid>
                
                {/* Protect */}
                <Typography variant="body2" gutterBottom sx={{ mt: 1 }}>
                  Bo v m ni (Protect)
                </Typography>
                <Grid container spacing={2} alignItems="center">
                  <Grid item xs>
                    <Slider
                      value={protectValue}
                      onChange={(e, newValue) => setProtectValue(newValue)}
                      min={0}
                      max={0.5}
                      step={0.01}
                      valueLabelDisplay="auto"
                    />
                  </Grid>
                  <Grid item>
                    <Typography variant="body2">
                      {protectValue.toFixed(2)}
                    </Typography>
                  </Grid>
                </Grid>
                
                {/* RMS Mix Rate */}
                <Typography variant="body2" gutterBottom sx={{ mt: 1 }}>
                  T l trn RMS
                </Typography>
                <Grid container spacing={2} alignItems="center">
                  <Grid item xs>
                    <Slider
                      value={rmsRate}
                      onChange={(e, newValue) => setRmsRate(newValue)}
                      min={0}
                      max={1}
                      step={0.01}
                      valueLabelDisplay="auto"
                    />
                  </Grid>
                  <Grid item>
                    <Typography variant="body2">
                      {rmsRate.toFixed(2)}
                    </Typography>
                  </Grid>
                </Grid>
              </CardContent>
            </Card>
          )}
          
          {/* Nt chuyn i */}
          <Button
            variant="contained"
            fullWidth
            sx={{ mt: 2 }}
            onClick={handleConvert}
            disabled={isLoading || !audioFile || !selectedVoice}
          >
            {isLoading ? (
              <>
                <CircularProgress size={24} sx={{ mr: 1 }} color="inherit" />
                ang x l...
              </>
            ) : (
              'Chuyn i ging ni'
            )}
          </Button>
          
          {/* Kt qu */}
          {result && (
            <Paper elevation={1} sx={{ p: 2, mt: 3 }}>
              <Typography variant="h6" gutterBottom>
                Kt qu chuyn i
              </Typography>
              
              <Box sx={{ mt: 2, display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                <Typography>
                   chuyn i thnh cng ging ni sang "{result.target_voice}"
                </Typography>
                
                <Box>
                  <IconButton color="primary" onClick={handlePlayResultAudio}>
                    {isResultPlaying ? <StopIcon /> : <VolumeUpIcon />}
                  </IconButton>
                  
                  <IconButton color="primary" onClick={() => handleDownload(result.result_url)}>
                    <DownloadIcon />
                  </IconButton>
                </Box>
              </Box>
              
              {/* Thng tin chi tit */}
              <Box sx={{ mt: 2 }}>
                <Divider sx={{ my: 1 }} />
                <Typography variant="body2">
                  <strong>Model:</strong> RVC
                </Typography>
                <Typography variant="body2">
                  <strong> cao (Pitch):</strong> {f0UpKey > 0 ? `+${f0UpKey}` : f0UpKey}
                </Typography>
                {showAdvanced && (
                  <>
                    <Typography variant="body2">
                      <strong>Index Rate:</strong> {indexRate.toFixed(2)}
                    </Typography>
                    <Typography variant="body2">
                      <strong>Protect:</strong> {protectValue.toFixed(2)}
                    </Typography>
                    <Typography variant="body2">
                      <strong>RMS Mix Rate:</strong> {rmsRate.toFixed(2)}
                    </Typography>
                  </>
                )}
              </Box>
            </Paper>
          )}
        </TabPanel>
        
        {/* Tab 2: UVR5 - Tch ging ni */}
        <TabPanel value={activeTab} index={1}>
          <Typography variant="h6" gutterBottom>
            Tch ging ni v nhc nn (UVR5)
          </Typography>
          
          {/* Chn file m thanh */}
          <Grid container spacing={2} sx={{ mt: 1, mb: 2 }}>
            <Grid item xs={12} md={6}>
              <Button
                variant="outlined"
                component="label"
                startIcon={<CloudUploadIcon />}
                fullWidth
                sx={{ height: '56px' }}
              >
                Chn file m thanh
                <input
                  type="file"
                  accept="audio/*"
                  hidden
                  onChange={handleUvrFileChange}
                />
              </Button>
            </Grid>
            <Grid item xs={12} md={6}>
              <Paper variant="outlined" sx={{ p: 1, display: 'flex', alignItems: 'center', height: '56px' }}>
                {uvrFile ? (
                  <>
                    <AudioFileIcon color="primary" sx={{ mx: 1 }} />
                    <Typography variant="body2" sx={{ flex: 1, overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                      {uvrFile.name}
                    </Typography>
                  </>
                ) : (
                  <Typography variant="body2" color="textSecondary" sx={{ mx: 1 }}>
                    Cha chn file no
                  </Typography>
                )}
              </Paper>
            </Grid>
          </Grid>
          
          {/* Chn loi tch */}
          <FormControl fullWidth sx={{ mb: 2 }}>
            <InputLabel>Loi tch</InputLabel>
            <Select
              value={vocalType}
              onChange={(e) => setVocalType(e.target.value)}
              label="Loi tch"
            >
              <MenuItem value="vocals">Ch tch ging ni</MenuItem>
              <MenuItem value="instrumental">Ch tch nhc nn</MenuItem>
              <MenuItem value="both">Tch c hai</MenuItem>
            </Select>
          </FormControl>
          
          {/* Nt tch ging ni */}
          <Button
            variant="contained"
            fullWidth
            sx={{ mt: 2 }}
            onClick={handleSeparateVocals}
            disabled={isUvrLoading || !uvrFile}
          >
            {isUvrLoading ? (
              <>
                <CircularProgress size={24} sx={{ mr: 1 }} color="inherit" />
                ang x l...
              </>
            ) : (
              'Tch ging ni'
            )}
          </Button>
          
          {/* Kt qu UVR5 */}
          {uvrResult && (
            <Paper elevation={1} sx={{ p: 2, mt: 3 }}>
              <Typography variant="h6" gutterBottom>
                Kt qu tch ging ni
              </Typography>
              
              {/* Hin th vocals nu c */}
              {uvrResult.vocals_url && (
                <Box sx={{ mt: 2, display: 'flex', alignItems: 'center', justifyContent: 'space-between', p: 1, bgcolor: 'background.paper', borderRadius: 1 }}>
                  <Typography>
                    <strong>Ging ni (Vocals)</strong>
                  </Typography>
                  
                  <Box>
                    <IconButton color="primary" onClick={handlePlayVocals}>
                      {isVocalsPlaying ? <StopIcon /> : <VolumeUpIcon />}
                    </IconButton>
                    
                    <IconButton color="primary" onClick={() => handleDownload(uvrResult.vocals_url)}>
                      <DownloadIcon />
                    </IconButton>
                  </Box>
                </Box>
              )}
              
              {/* Hin th instrumental nu c */}
              {uvrResult.instrumental_url && (
                <Box sx={{ mt: 2, display: 'flex', alignItems: 'center', justifyContent: 'space-between', p: 1, bgcolor: 'background.paper', borderRadius: 1 }}>
                  <Typography>
                    <strong>Nhc nn (Instrumental)</strong>
                  </Typography>
                  
                  <Box>
                    <IconButton color="primary" onClick={handlePlayInstrumental}>
                      {isInstrumentalPlaying ? <StopIcon /> : <VolumeUpIcon />}
                    </IconButton>
                    
                    <IconButton color="primary" onClick={() => handleDownload(uvrResult.instrumental_url)}>
                      <DownloadIcon />
                    </IconButton>
                  </Box>
                </Box>
              )}
            </Paper>
          )}
        </TabPanel>
        
        {/* Tab 3: Trch xut F0 */}
        <TabPanel value={activeTab} index={2}>
          <Typography variant="h6" gutterBottom>
            Trch xut ng cong F0
          </Typography>
          
          {/* Chn file m thanh */}
          <Grid container spacing={2} sx={{ mt: 1, mb: 2 }}>
            <Grid item xs={12} md={6}>
              <Button
                variant="outlined"
                component="label"
                startIcon={<CloudUploadIcon />}
                fullWidth
                sx={{ height: '56px' }}
              >
                Chn file m thanh
                <input
                  type="file"
                  accept="audio/*"
                  hidden
                  onChange={handleF0FileChange}
                />
              </Button>
            </Grid>
            <Grid item xs={12} md={6}>
              <Paper variant="outlined" sx={{ p: 1, display: 'flex', alignItems: 'center', height: '56px' }}>
                {f0File ? (
                  <>
                    <AudioFileIcon color="primary" sx={{ mx: 1 }} />
                    <Typography variant="body2" sx={{ flex: 1, overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                      {f0File.name}
                    </Typography>
                  </>
                ) : (
                  <Typography variant="body2" color="textSecondary" sx={{ mx: 1 }}>
                    Cha chn file no
                  </Typography>
                )}
              </Paper>
            </Grid>
          </Grid>
          
          {/* Chn phng php F0 */}
          <FormControl fullWidth sx={{ mb: 2 }}>
            <InputLabel>Phng php trch xut F0</InputLabel>
            <Select
              value={f0Method}
              onChange={(e) => setF0Method(e.target.value)}
              label="Phng php trch xut F0"
            >
              <MenuItem value="dio">DIO (Nhanh)</MenuItem>
              <MenuItem value="harvest">Harvest (Chnh xc)</MenuItem>
              <MenuItem value="crepe">Crepe (Dng GPU, tt nht)</MenuItem>
            </Select>
          </FormControl>
          
          {/* Nt trch xut F0 */}
          <Button
            variant="contained"
            fullWidth
            sx={{ mt: 2 }}
            onClick={handleExtractF0}
            disabled={isF0Loading || !f0File}
          >
            {isF0Loading ? (
              <>
                <CircularProgress size={24} sx={{ mr: 1 }} color="inherit" />
                ang x l...
              </>
            ) : (
              'Trch xut F0'
            )}
          </Button>
          
          {/* Kt qu F0 */}
          {f0Result && (
            <Paper elevation={1} sx={{ p: 2, mt: 3 }}>
              <Typography variant="h6" gutterBottom>
                Kt qu trch xut F0
              </Typography>
              
              <Box sx={{ mt: 2, display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                <Typography>
                   trch xut F0 thnh cng
                </Typography>
                
                <Button
                  variant="outlined"
                  startIcon={<DownloadIcon />}
                  onClick={() => handleDownload(f0Result.f0_url)}
                >
                  Ti xung F0 (.npy)
                </Button>
              </Box>
            </Paper>
          )}
        </TabPanel>
        
        {/* Tab 4: Hun luyn Model */}
        <TabPanel value={activeTab} index={3}>
          <Typography variant="h6" gutterBottom>
            Hun luyn Model RVC mi
          </Typography>
          
          <Alert severity="info" sx={{ mb: 2 }}>
            Upload file ZIP cha cc file m thanh  hun luyn model ging ni mi.
            Dataset nn c t nht 5-10 pht ging ni cht lng tt.
          </Alert>
          
          {/* Chn file dataset */}
          <Grid container spacing={2} sx={{ mt: 1, mb: 2 }}>
            <Grid item xs={12} md={6}>
              <Button
                variant="outlined"
                component="label"
                startIcon={<FolderZipIcon />}
                fullWidth
                sx={{ height: '56px' }}
              >
                Chn file ZIP dataset
                <input
                  type="file"
                  accept=".zip"
                  hidden
                  onChange={handleDatasetFileChange}
                />
              </Button>
            </Grid>
            <Grid item xs={12} md={6}>
              <Paper variant="outlined" sx={{ p: 1, display: 'flex', alignItems: 'center', height: '56px' }}>
                {datasetFile ? (
                  <>
                    <FolderZipIcon color="primary" sx={{ mx: 1 }} />
                    <Typography variant="body2" sx={{ flex: 1, overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                      {datasetFile.name}
                    </Typography>
                  </>
                ) : (
                  <Typography variant="body2" color="textSecondary" sx={{ mx: 1 }}>
                    Cha chn file no
                  </Typography>
                )}
              </Paper>
            </Grid>
          </Grid>
          
          {/* Nhp tn model */}
          <TextField
            fullWidth
            label="Tn Model"
            value={modelName}
            onChange={(e) => setModelName(e.target.value)}
            margin="normal"
            variant="outlined"
            helperText="t tn cho model ca bn"
          />
          
          {/* Tham s hun luyn */}
          <Card variant="outlined" sx={{ mt: 2, mb: 2 }}>
            <CardContent>
              <Typography variant="subtitle2" gutterBottom>
                Tham s hun luyn
              </Typography>
              
              {/* Epochs */}
              <Typography variant="body2" gutterBottom>
                S epochs
              </Typography>
              <Grid container spacing={2} alignItems="center">
                <Grid item xs>
                  <Slider
                    value={epochs}
                    onChange={(e, newValue) => setEpochs(newValue)}
                    min={50}
                    max={500}
                    step={10}
                    valueLabelDisplay="auto"
                  />
                </Grid>
                <Grid item>
                  <Typography variant="body2">
                    {epochs}
                  </Typography>
                </Grid>
              </Grid>
              
              {/* Batch size */}
              <Typography variant="body2" gutterBottom sx={{ mt: 1 }}>
                Batch size
              </Typography>
              <Grid container spacing={2} alignItems="center">
                <Grid item xs>
                  <Slider
                    value={batchSize}
                    onChange={(e, newValue) => setBatchSize(newValue)}
                    min={1}
                    max={16}
                    step={1}
                    valueLabelDisplay="auto"
                  />
                </Grid>
                <Grid item>
                  <Typography variant="body2">
                    {batchSize}
                  </Typography>
                </Grid>
              </Grid>
              
              {/* Phng php F0 */}
              <FormControl fullWidth sx={{ mt: 2 }}>
                <InputLabel>Phng php F0</InputLabel>
                <Select
                  value={trainF0Method}
                  onChange={(e) => setTrainF0Method(e.target.value)}
                  label="Phng php F0"
                >
                  <MenuItem value="dio">DIO (Nhanh)</MenuItem>
                  <MenuItem value="harvest">Harvest (Chnh xc)</MenuItem>
                  <MenuItem value="crepe">Crepe (Dng GPU, tt nht)</MenuItem>
                </Select>
              </FormControl>
            </CardContent>
          </Card>
          
          {/* Nt hun luyn */}
          <Button
            variant="contained"
            fullWidth
            sx={{ mt: 2 }}
            onClick={handleTrainModel}
            disabled={isTraining || !datasetFile || !modelName}
          >
            {isTraining ? (
              <>
                <CircularProgress size={24} sx={{ mr: 1 }} color="inherit" />
                ang hun luyn... (C th mt nhiu pht)
              </>
            ) : (
              'Hun luyn Model'
            )}
          </Button>
          
          {/* Kt qu train model */}
          {trainResult && (
            <Paper elevation={1} sx={{ p: 2, mt: 3 }}>
              <Typography variant="h6" gutterBottom>
                Kt qu hun luyn model
              </Typography>
              
              <Box sx={{ mt: 2 }}>
                <Alert severity="success">
                   hun luyn model thnh cng!
                </Alert>
                
                <Typography variant="body1" sx={{ mt: 2 }}>
                  Model: <strong>{trainResult.model_name}</strong>
                </Typography>
                
                <Typography variant="body2" sx={{ mt: 1 }}>
                  Model  c lu v sn sng s dng. Bn c th s dng model ny trong tab chuyn i ging ni.
                </Typography>
              </Box>
            </Paper>
          )}
        </TabPanel>
      </Paper>
      
      {/* Thng bo li chung */}
      {error && (
        <Alert severity="error" sx={{ mt: 2 }}>
          {error}
        </Alert>
      )}
    </Box>
  );
};

export default RVCTab; 