import React, { useState, useEffect, useRef } from 'react';
import { 
  Container, Typography, Box, Button, Paper, 
  Grid, FormControl, InputLabel, Select, MenuItem,
  CircularProgress, Alert, IconButton, Slider, Card,
  CardContent, Tabs, Tab, Divider, Chip, List, ListItem,
  ListItemText, ListItemIcon, ListItemSecondaryAction, Tooltip
} from '@mui/material';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import AudioFileIcon from '@mui/icons-material/AudioFile';
import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline';
import VolumeUpIcon from '@mui/icons-material/VolumeUp';
import StopIcon from '@mui/icons-material/Stop';
import InfoIcon from '@mui/icons-material/Info';
import HistoryIcon from '@mui/icons-material/History';
import DownloadIcon from '@mui/icons-material/Download';
import CompareIcon from '@mui/icons-material/Compare';
import PersonIcon from '@mui/icons-material/Person';
import TextsmsIcon from '@mui/icons-material/Textsms';
import { VoiceAPI } from '../../services/api';
import TextToSpeechTab from './TextToSpeechTab';

// Component hin th thng tin chi tit ging ni
const VoiceDetailCard = ({ voicePath }) => {
  const [voiceDetails, setVoiceDetails] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    const fetchDetails = async () => {
      if (!voicePath) return;
      
      setLoading(true);
      try {
        const response = await VoiceAPI.getVoiceDetails(voicePath);
        setVoiceDetails(response.data);
        setError('');
      } catch (err) {
        console.error('Li khi ly thng tin ging ni:', err);
        setError('Khng th ly thng tin ging ni');
      } finally {
        setLoading(false);
      }
    };

    fetchDetails();
  }, [voicePath]);

  if (loading) return <CircularProgress size={20} />;
  if (error) return <Typography color="error">{error}</Typography>;
  if (!voiceDetails) return null;

  return (
    <Card variant="outlined" sx={{ mt: 2, mb: 2 }}>
      <CardContent>
        <Typography variant="h6" gutterBottom>
          Thng tin ging ni
        </Typography>
        <Grid container spacing={1}>
          <Grid item xs={6}>
            <Typography variant="body2">Tn file: {voiceDetails.filename}</Typography>
          </Grid>
          <Grid item xs={6}>
            <Typography variant="body2"> di: {voiceDetails.duration}s</Typography>
          </Grid>
          <Grid item xs={6}>
            <Typography variant="body2">Sample rate: {voiceDetails.sample_rate}Hz</Typography>
          </Grid>
          <Grid item xs={6}>
            <Typography variant="body2">Kch thc: {voiceDetails.file_size_mb}MB</Typography>
          </Grid>
          {voiceDetails.voice_type && (
            <>
              <Grid item xs={6}>
                <Typography variant="body2">Loi ging: {voiceDetails.voice_type}</Typography>
              </Grid>
              <Grid item xs={6}>
                <Typography variant="body2">m vc: {voiceDetails.voice_range}</Typography>
              </Grid>
              <Grid item xs={12}>
                <Tooltip title=" cao ca ging (Hz)">
                  <Typography variant="body2">
                     cao trung bnh: {voiceDetails.average_pitch}Hz
                  </Typography>
                </Tooltip>
              </Grid>
            </>
          )}
        </Grid>
      </CardContent>
    </Card>
  );
};

// Component hin th lch s chuyn i
const ConversionHistory = () => {
  const [history, setHistory] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const fetchHistory = async () => {
    setLoading(true);
    try {
      const response = await VoiceAPI.getConversionHistory();
      setHistory(response.data);
      setError('');
    } catch (err) {
      console.error('Li khi ly lch s chuyn i:', err);
      setError('Khng th ly lch s chuyn i');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchHistory();
  }, []);

  const formatDate = (timestamp) => {
    return new Date(timestamp * 1000).toLocaleString('vi-VN');
  };

  if (loading) return <CircularProgress />;
  if (error) return <Typography color="error">{error}</Typography>;
  if (history.length === 0) return <Typography>Cha c lch s chuyn i</Typography>;

  return (
    <List>
      {history.map((item, index) => (
        <React.Fragment key={index}>
          <ListItem>
            <ListItemIcon>
              <AudioFileIcon />
            </ListItemIcon>
            <ListItemText 
              primary={`${item.source_file}  ${item.target_voice.split('/').pop()}`}
              secondary={`${formatDate(item.timestamp)} - Tau: ${item.tau}`}
            />
            <ListItemSecondaryAction>
              <IconButton edge="end" onClick={() => VoiceAPI.downloadResult(item.result_url)}>
                <DownloadIcon />
              </IconButton>
              <IconButton edge="end" onClick={() => VoiceAPI.playAudioFile(item.result_url)}>
                <VolumeUpIcon />
              </IconButton>
            </ListItemSecondaryAction>
          </ListItem>
          {index < history.length - 1 && <Divider />}
        </React.Fragment>
      ))}
    </List>
  );
};

const UserDashboard = () => {
  // State variables
  const [audioFile, setAudioFile] = useState(null);
  const [audioPreviewUrl, setAudioPreviewUrl] = useState(null);
  const [availableVoices, setAvailableVoices] = useState([]);
  const [selectedVoice, setSelectedVoice] = useState('');
  const [tauValue, setTauValue] = useState(0.4);
  const [isLoading, setIsLoading] = useState(false);
  const [result, setResult] = useState(null);
  const [error, setError] = useState('');
  const [isModelLoading, setIsModelLoading] = useState(true);
  const [currentTab, setCurrentTab] = useState(0);
  const [showVoiceDetails, setShowVoiceDetails] = useState(false);
  const [processingProgress, setProcessingProgress] = useState(0);
  const [processingStatus, setProcessingStatus] = useState('');
  const [playbackSpeed, setPlaybackSpeed] = useState(1.0);
  const [audioProgress, setAudioProgress] = useState(0);
  const [audioDuration, setAudioDuration] = useState(0);
  
  // Trng thi pht m thanh
  const [isSourcePlaying, setIsSourcePlaying] = useState(false);
  const [isTargetPlaying, setIsTargetPlaying] = useState(false);
  const [isResultPlaying, setIsResultPlaying] = useState(false);
  
  // Tham chiu n cc i tng Audio
  const sourceAudioRef = useRef(null);
  const targetAudioRef = useRef(null);
  const resultAudioRef = useRef(null);

  // Ti danh sch ging ni t backend
  useEffect(() => {
    const fetchVoices = async () => {
      try {
        const response = await VoiceAPI.getModels();
        if (response.data.openvoice && response.data.openvoice.length > 0) {
          setAvailableVoices(response.data.openvoice);
          setSelectedVoice(response.data.openvoice[0]);
          setIsModelLoading(false);
        } else {
          setError('Khng tm thy mu ging ni no');
        }
      } catch (err) {
        console.error('Li khi ti danh sch ging ni:', err);
        setError('Khng th kt ni vi my ch. Vui lng th li sau.');
      }
    };

    fetchVoices();
  }, []);

  // X l khi ngi dng chn file
  const handleFileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      if (file.type.includes('audio') || file.name.match(/\.(wav|mp3|flac)$/)) {
        // Nu ang pht m thanh, dng trc khi thay i file
        if (sourceAudioRef.current) {
          sourceAudioRef.current.pause();
          sourceAudioRef.current = null;
          setIsSourcePlaying(false);
        }
        
        // Gii phng URL c nu c
        if (audioPreviewUrl) {
          URL.revokeObjectURL(audioPreviewUrl);
        }
        
        setAudioFile(file);
        setError('');
        
        // To URL cho file audio  nghe th
        const fileUrl = URL.createObjectURL(file);
        setAudioPreviewUrl(fileUrl);
        
        console.log(` to URL mi cho file: ${file.name}, URL: ${fileUrl}`);
      } else {
        setError('Vui lng chn file m thanh hp l (WAV, MP3, FLAC)');
        setAudioFile(null);
        setAudioPreviewUrl(null);
      }
    }
  };

  // X l khi ngi dng thay i la chn ging ni
  const handleVoiceChange = (event) => {
    setSelectedVoice(event.target.value);
  };

  // X l khi ngi dng thay i gi tr tau
  const handleTauChange = (event, newValue) => {
    setTauValue(newValue);
  };

  // X l khi ngi dng gi yu cu chuyn i
  const handleConvert = async () => {
    if (!audioFile) {
      setError('Vui lng chn file m thanh');
      return;
    }

    if (!selectedVoice) {
      setError('Vui lng chn ging ni mc tiu');
      return;
    }

    setIsLoading(true);
    setResult(null);
    setError('');
    setProcessingProgress(0);
    setProcessingStatus('ang chun b x l...');

    // To form data  gi ln server
    const formData = new FormData();
    formData.append('audio', audioFile);
    formData.append('model_type', 'openvoice');
    formData.append('target_voice', selectedVoice);
    formData.append('tau', tauValue);

    // Gi lp tin trnh x l
    let progressInterval = setInterval(() => {
      setProcessingProgress((prev) => {
        if (prev >= 90) {
          clearInterval(progressInterval);
          return prev;
        }
        
        const increment = Math.random() * 10;
        const newValue = Math.min(prev + increment, 90);
        
        // Cp nht trng thi da trn tin trnh
        if (newValue < 20) {
          setProcessingStatus('ang ti file m thanh...');
        } else if (newValue < 40) {
          setProcessingStatus('ang phn tch c trng m thanh ngun...');
        } else if (newValue < 60) {
          setProcessingStatus('ang phn tch c trng ging ni ch...');
        } else if (newValue < 80) {
          setProcessingStatus('ang chuyn i ging ni...');
        } else {
          setProcessingStatus('ang x l kt qu...');
        }
        
        return newValue;
      });
    }, 1000);

    try {
      const response = await VoiceAPI.convertVoice(formData);

      if (response.data.success) {
        // t tin trnh v 100% khi hon thnh
        clearInterval(progressInterval);
        setProcessingProgress(100);
        setProcessingStatus('Hon thnh!');
        
        setResult(response.data);
      } else {
        clearInterval(progressInterval);
        setError(response.data.error || 'C li xy ra khi x l yu cu');
      }
    } catch (err) {
      clearInterval(progressInterval);
      console.error('Li khi gi yu cu chuyn i:', err);
      setError(err.response?.data?.error || 'Khng th kt ni vi my ch');
    } finally {
      setIsLoading(false);
    }
  };

  // X l khi ngi dng ti xung kt qu
  const handleDownload = () => {
    if (result && result.result_url) {
      VoiceAPI.downloadResult(result.result_url);
    }
  };

  // X l khi ngi dng chuyn tab
  const handleTabChange = (event, newValue) => {
    setCurrentTab(newValue);
  };

  // X l nghe th file ngun
  const handlePlaySourceAudio = () => {
    // Dng cc audio khc nu ang pht
    if (targetAudioRef.current) {
      targetAudioRef.current.pause();
      setIsTargetPlaying(false);
    }
    if (resultAudioRef.current) {
      resultAudioRef.current.pause();
      setIsResultPlaying(false);
    }

    if (isSourcePlaying && sourceAudioRef.current) {
      // Nu ang pht, dng li
      sourceAudioRef.current.pause();
      setIsSourcePlaying(false);
    } else if (audioPreviewUrl) {
      // Lun to i tng Audio mi vi URL hin ti  m bo pht ng file
      if (sourceAudioRef.current) {
        sourceAudioRef.current.pause();
        sourceAudioRef.current = null;
      }
      
      console.log(`Pht audio t URL: ${audioPreviewUrl}`);
      sourceAudioRef.current = new Audio(audioPreviewUrl);
      
      // X l s kin khi pht xong
      sourceAudioRef.current.onended = () => {
        setIsSourcePlaying(false);
      };
      
      // Thm x l li
      sourceAudioRef.current.onerror = (error) => {
        console.error('Li khi pht audio:', error);
        setIsSourcePlaying(false);
        setError('Khng th pht file m thanh ny, vui lng th file khc');
      };
      
      sourceAudioRef.current.play()
        .then(() => {
          setIsSourcePlaying(true);
        })
        .catch(error => {
          console.error('Li khi pht audio:', error);
          setIsSourcePlaying(false);
          setError('Khng th pht file m thanh ny, vui lng th file khc');
        });
    }
  };

  // X l nghe th ging ni ch
  const handlePlayTargetVoice = () => {
    // Dng cc audio khc nu ang pht
    if (sourceAudioRef.current) {
      sourceAudioRef.current.pause();
      setIsSourcePlaying(false);
    }
    if (resultAudioRef.current) {
      resultAudioRef.current.pause();
      setIsResultPlaying(false);
    }

    if (isTargetPlaying && targetAudioRef.current) {
      // Nu ang pht, dng li
      targetAudioRef.current.pause();

      targetAudioRef.current = null; // Reset tham chiu khi dng pht
      setIsTargetPlaying(false);
    } else if (selectedVoice) {
      // Nu cha pht, bt u pht
      const encodedPath = encodeURIComponent(selectedVoice);
      const audioUrl = `/api/play-audio?path=${encodedPath}`;
      
      // To mi i tng Audio mi ln pht  trnh cache
      targetAudioRef.current = new Audio(audioUrl);
      targetAudioRef.current.onended = () => {
        targetAudioRef.current = null; // Reset tham chiu khi pht xong
        setIsTargetPlaying(false);
      };
      
      // Thm s kin x l li
      targetAudioRef.current.onerror = (error) => {
        console.error('Li khi pht audio:', error);
        targetAudioRef.current = null;
        setIsTargetPlaying(false);
        setError('Khng th pht file audio, vui lng th li sau.');
      };
      
      targetAudioRef.current.play().catch(error => {
        console.error('Li khi pht audio:', error);
        targetAudioRef.current = null;
        setIsTargetPlaying(false);
        setError('Khng th pht file audio, vui lng th li sau.');
      });
      
      setIsTargetPlaying(true);
    }
  };

  // Reset audio khi result thay i
  useEffect(() => {
    if (result) {
      // Dng pht nu ang pht
      if (resultAudioRef.current) {
        resultAudioRef.current.pause();
        resultAudioRef.current = null;
      }
      setIsResultPlaying(false);
      setAudioProgress(0);
      setAudioDuration(0);
      console.log("C kt qu mi,  reset player");
    }
  }, [result]);

  // X l pht m thanh kt qu
  const handlePlayResultAudio = () => {
    // Dng cc audio khc nu ang pht
    if (sourceAudioRef.current) {
      sourceAudioRef.current.pause();
      setIsSourcePlaying(false);
    }
    if (targetAudioRef.current) {
      targetAudioRef.current.pause();
      setIsTargetPlaying(false);
    }

    if (isResultPlaying && resultAudioRef.current) {
      // Nu ang pht, dng li
      resultAudioRef.current.pause();
      resultAudioRef.current = null;
      setIsResultPlaying(false);
    } else if (result && result.result_url) {
      // Lun to mi i tng Audio  m bo pht ng file
      if (resultAudioRef.current) {
        resultAudioRef.current.pause();
        resultAudioRef.current = null;
      }
      
      // Thm timestamp vo URL  trnh cache
      const audioUrl = `${result.result_url}${result.result_url.includes('?') ? '&' : '?'}t=${Date.now()}`;
      console.log(`Pht kt qu t URL: ${audioUrl}`);
      
      resultAudioRef.current = new Audio(audioUrl);
      resultAudioRef.current.playbackRate = playbackSpeed;
      
      // Lng nghe s kin metadata  ly thi lng audio
      resultAudioRef.current.onloadedmetadata = () => {
        setAudioDuration(resultAudioRef.current.duration);
      };
      
      // Lng nghe s kin timeupdate  cp nht tin trnh
      resultAudioRef.current.ontimeupdate = () => {
        setAudioProgress(resultAudioRef.current.currentTime);
      };
      
      // X l s kin khi pht xong
      resultAudioRef.current.onended = () => {
        setAudioProgress(0);
        resultAudioRef.current = null;
        setIsResultPlaying(false);
      };
      
      // Thm x l li
      resultAudioRef.current.onerror = (error) => {
        console.error('Li khi pht audio kt qu:', error);
        resultAudioRef.current = null;
        setIsResultPlaying(false);
        setError('Khng th pht file kt qu, vui lng th li sau.');
      };
      
      resultAudioRef.current.play()
        .then(() => {
          setIsResultPlaying(true);
        })
        .catch(error => {
          console.error('Li khi pht audio kt qu:', error);
          resultAudioRef.current = null;
          setIsResultPlaying(false);
          setError('Khng th pht file kt qu, vui lng th li sau.');
        });
    }
  };

  // X l khi ngi dng tua audio
  const handleAudioSeek = (event, newValue) => {
    setAudioProgress(newValue);
    if (resultAudioRef.current) {
      resultAudioRef.current.currentTime = newValue;
    }
  };

  // X l thay i tc  pht
  const handleSpeedChange = (event, newValue) => {
    setPlaybackSpeed(newValue);
    // p dng tc  mi nu ang pht
    if (resultAudioRef.current && isResultPlaying) {
      resultAudioRef.current.playbackRate = newValue;
    }
  };

  // Gii phng ti nguyn khi component unmount
  useEffect(() => {
    return () => {
      // Gii phng cc URL  to  trnh r r b nh
      if (audioPreviewUrl) {
        URL.revokeObjectURL(audioPreviewUrl);
      }
      
      // Dng v gii phng cc i tng Audio
      if (sourceAudioRef.current) {
        sourceAudioRef.current.pause();
        sourceAudioRef.current = null;
      }
      if (targetAudioRef.current) {
        targetAudioRef.current.pause();
        targetAudioRef.current = null;
      }
      if (resultAudioRef.current) {
        resultAudioRef.current.pause();
        resultAudioRef.current = null;
      }
    };
  }, [audioPreviewUrl]);

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      <Typography variant="h4" gutterBottom sx={{ mb: 3 }}>
        Chuyn i ging ni
      </Typography>
      
      <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 3 }}>
        <Tabs 
          value={currentTab} 
          onChange={handleTabChange}
          aria-label="voice conversion tabs"
        >
          <Tab label="Chuyn i ging ni" icon={<CompareIcon />} iconPosition="start" />
          <Tab label="Text-to-Speech" icon={<TextsmsIcon />} iconPosition="start" />
          <Tab label="Lch s" icon={<HistoryIcon />} iconPosition="start" />
        </Tabs>
      </Box>
      
      {/* Tab chuyn i ging ni */}
      {currentTab === 0 && (
        <Box>
          <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
            <Typography variant="h6" gutterBottom>
              Ti ln file m thanh ca bn
            </Typography>
            <Grid container spacing={3}>
              <Grid item xs={12}>
                <Box sx={{ display: 'flex', justifyContent: 'center', p: 3, border: '2px dashed grey', borderRadius: 2 }}>
                  <input
                    accept="audio/*"
                    style={{ display: 'none' }}
                    id="contained-button-file"
                    type="file"
                    onChange={handleFileChange}
                    disabled={isLoading}
                  />
                  <label htmlFor="contained-button-file">
                    <Button 
                      variant="contained" 
                      component="span"
                      startIcon={<CloudUploadIcon />}
                      disabled={isLoading}
                    >
                      Chn file m thanh
                    </Button>
                  </label>
                </Box>
                {audioFile && (
                  <Box sx={{ mt: 2 }}>
                    <Typography variant="body1">
                      File  chn: {audioFile.name}
                    </Typography>
                    <Box sx={{ display: 'flex', alignItems: 'center', mt: 1 }}>
                      <IconButton 
                        color="primary" 
                        onClick={handlePlaySourceAudio}
                        disabled={isLoading}
                        aria-label={isSourcePlaying ? 'Dng pht' : 'Pht audio'}
                      >
                        {isSourcePlaying ? <StopIcon /> : <PlayCircleOutlineIcon />}
                      </IconButton>
                      <Typography variant="body2">
                        {isSourcePlaying ? 'ang pht...' : 'Nghe th'}
                      </Typography>
                    </Box>
                  </Box>
                )}
              </Grid>
              
              <Grid item xs={12} md={6}>
                <FormControl fullWidth>
                  <InputLabel id="voice-select-label">Ging ni mc tiu</InputLabel>
                  <Select
                    labelId="voice-select-label"
                    id="voice-select"
                    value={selectedVoice}
                    label="Ging ni mc tiu"
                    onChange={handleVoiceChange}
                    disabled={isLoading || availableVoices.length === 0}
                  >
                    {availableVoices.map((voice) => (
                      <MenuItem key={voice} value={voice}>
                        {voice.split('/').pop()} {/* Hin th tn file khng c ng dn */}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
                {selectedVoice && (
                  <Box sx={{ display: 'flex', alignItems: 'center', mt: 1 }}>
                    <IconButton 
                      color="primary" 
                      onClick={handlePlayTargetVoice}
                      disabled={isLoading}
                      aria-label={isTargetPlaying ? 'Dng pht' : 'Nghe ging mc tiu'}
                    >
                      {isTargetPlaying ? <StopIcon /> : <VolumeUpIcon />}
                    </IconButton>
                    <Typography variant="body2">
                      {isTargetPlaying ? 'ang pht...' : 'Nghe th ging mc tiu'}
                    </Typography>
                    <IconButton 
                      color="info" 
                      onClick={() => setShowVoiceDetails(!showVoiceDetails)}
                      aria-label="Xem thng tin chi tit"
                      sx={{ ml: 'auto' }}
                    >
                      <InfoIcon />
                    </IconButton>
                  </Box>
                )}
              </Grid>
              
              <Grid item xs={12} md={6}>
                <Typography id="tau-slider" gutterBottom>
                  Tau ( ging ging mc tiu): {tauValue}
                </Typography>
                <Slider
                  aria-labelledby="tau-slider"
                  value={tauValue}
                  onChange={handleTauChange}
                  min={0.1}
                  max={1.0}
                  step={0.05}
                  marks={[
                    { value: 0.1, label: '0.1' },
                    { value: 0.4, label: '0.4' },
                    { value: 0.7, label: '0.7' },
                    { value: 1.0, label: '1.0' },
                  ]}
                  disabled={isLoading}
                />
                <Typography variant="body2" color="textSecondary">
                  Gi tr thp: gi nhiu c im t ging gc. Gi tr cao: ging hn vi ging mc tiu.
                </Typography>
              </Grid>
              
              <Grid item xs={12}>
                <Box sx={{ display: 'flex', justifyContent: 'center', mt: 2 }}>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={handleConvert}
                    disabled={isLoading || !audioFile || !selectedVoice}
                  >
                    {isLoading ? (
                      <>
                        <CircularProgress size={24} sx={{ mr: 1 }} />
                        ang x l...
                      </>
                    ) : (
                      'Chuyn i ging ni'
                    )}
                  </Button>
                </Box>
              </Grid>
            </Grid>
            
            {error && (
              <Alert severity="error" sx={{ mt: 2 }}>
                {error}
              </Alert>
            )}
            
            {isLoading && (
              <Box sx={{ mt: 2, textAlign: 'center' }}>
                <CircularProgress variant="determinate" value={processingProgress} />
                <Typography variant="body2" color="textSecondary" sx={{ mt: 1 }}>
                  {processingStatus || 'ang x l...'}
                </Typography>
              </Box>
            )}
            
            {showVoiceDetails && selectedVoice && (
              <VoiceDetailCard voicePath={selectedVoice} />
            )}
          </Paper>
          
          {result && (
            <Paper elevation={3} sx={{ p: 3 }}>
              <Typography variant="h6" gutterBottom>
                Kt qu chuyn i
              </Typography>
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                <IconButton 
                  color="primary" 
                  onClick={handlePlayResultAudio}
                  aria-label={isResultPlaying ? 'Dng pht' : 'Pht kt qu'}
                >
                  {isResultPlaying ? <StopIcon /> : <PlayCircleOutlineIcon />}
                </IconButton>
                <Typography variant="body2" sx={{ ml: 1, minWidth: '70px' }}>
                  {audioDuration > 0 
                    ? `${Math.floor(audioProgress / 60)}:${Math.floor(audioProgress % 60).toString().padStart(2, '0')} / ${Math.floor(audioDuration / 60)}:${Math.floor(audioDuration % 60).toString().padStart(2, '0')}`
                    : isResultPlaying ? 'ang pht...' : 'Nghe kt qu'}
                </Typography>
                
                <Box sx={{ flex: 1, mx: 2 }}>
                  <Slider
                    size="small"
                    value={audioProgress}
                    max={audioDuration || 100}
                    onChange={handleAudioSeek}
                    aria-label="V tr audio"
                    disabled={!isResultPlaying && audioProgress === 0}
                  />
                </Box>
                
                <Button
                  variant="outlined"
                  size="small"
                  startIcon={<DownloadIcon />}
                  onClick={handleDownload}
                >
                  Ti xung
                </Button>
              </Box>
              
              {/* iu chnh tc  pht */}
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                <Typography id="speed-slider" sx={{ width: '120px' }}>
                  Tc  pht: {playbackSpeed.toFixed(1)}x
                </Typography>
                <Slider
                  aria-labelledby="speed-slider"
                  value={playbackSpeed}
                  onChange={handleSpeedChange}
                  min={0.5}
                  max={2.0}
                  step={0.1}
                  marks={[
                    { value: 0.5, label: '0.5x' },
                    { value: 1.0, label: '1.0x' },
                    { value: 1.5, label: '1.5x' },
                    { value: 2.0, label: '2.0x' },
                  ]}
                  sx={{ ml: 2, width: 'calc(100% - 150px)' }}
                />
              </Box>
              
              <Divider sx={{ my: 2 }} />
              <Grid container spacing={2}>
                <Grid item xs={12} md={6}>
                  <Typography variant="body2">
                    <strong>File ngun:</strong> {audioFile?.name}
                  </Typography>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Typography variant="body2">
                    <strong>Ging mc tiu:</strong> {selectedVoice.split('/').pop()}
                  </Typography>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Typography variant="body2">
                    <strong>Tau:</strong> {tauValue}
                  </Typography>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Typography variant="body2">
                    <strong>Ngy to:</strong> {new Date().toLocaleString('vi-VN')}
                  </Typography>
                </Grid>
              </Grid>
            </Paper>
          )}
        </Box>
      )}
      
      {/* Tab Text-to-Speech */}
      {currentTab === 1 && <TextToSpeechTab />}
      
      {/* Tab lch s */}
      {currentTab === 2 && (
        <Paper elevation={3} sx={{ p: 3 }}>
          <Typography variant="h6" gutterBottom>
            Lch s chuyn i ging ni
          </Typography>
          <ConversionHistory />
        </Paper>
      )}
      
    </Container>
  );
};

export default UserDashboard; 