import React, { useState, useEffect } from 'react';
import {
  List, ListItem, ListItemIcon, ListItemText, ListItemSecondaryAction,
  IconButton, Divider, CircularProgress, Typography, Box, Alert
} from '@mui/material';
import TextsmsIcon from '@mui/icons-material/Textsms';
import VolumeUpIcon from '@mui/icons-material/VolumeUp';
import DownloadIcon from '@mui/icons-material/Download';
import { VoiceAPI } from '../../services/api';

const TtsHistory = () => {
  const [history, setHistory] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const fetchHistory = async () => {
    setLoading(true);
    try {
      const response = await VoiceAPI.getTtsHistory();
      setHistory(response.data);
      setError('');
    } catch (err) {
      console.error('Li khi ly lch s TTS:', err);
      setError('Khng th ly lch s TTS');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchHistory();
  }, []);

  const formatDate = (timestamp) => {
    return new Date(timestamp * 1000).toLocaleString('vi-VN');
  };

  // Truncate long text for display
  const truncateText = (text, maxLength = 50) => {
    if (!text) return '';
    if (text.length <= maxLength) return text;
    return text.substring(0, maxLength) + '...';
  };
  
  // nh x tn ngn ng  hin th
  const getLanguageDisplayName = (code) => {
    const languageMap = {
      'english': 'Ting Anh',
      'chinese': 'Ting Trung',
      'french': 'Ting Php',
      'spanish': 'Ting Ty Ban Nha',
      'japanese': 'Ting Nht',
      'korean': 'Ting Hn'
    };
    return languageMap[code] || code;
  };
  
  // nh x tn speaker  hin th d c
  const getSpeakerDisplayName = (speakerCode) => {
    const speakerMap = {
      'en-us': 'Ting Anh - M',
      'en-br': 'Ting Anh - Anh',
      'en-au': 'Ting Anh - c',
      'en-india': 'Ting Anh - n ',
      'en-default': 'Ting Anh - Mc nh',
      'en-newest': 'Ting Anh - Mi nht',
      'zh': 'Ting Trung',
      'jp': 'Ting Nht',
      'kr': 'Ting Hn',
      'fr': 'Ting Php',
      'es': 'Ting Ty Ban Nha'
    };
    return speakerMap[speakerCode] || speakerCode;
  };

  if (loading) return <Box sx={{ textAlign: 'center', py: 3 }}><CircularProgress /></Box>;
  
  if (error) return (
    <Alert severity="error" sx={{ mt: 2 }}>
      {error}
    </Alert>
  );
  
  if (history.length === 0) return (
    <Box sx={{ py: 2 }}>
      <Typography align="center" color="textSecondary">
        Cha c lch s chuyn i TTS
      </Typography>
    </Box>
  );

  return (
    <List>
      {history.map((item, index) => (
        <React.Fragment key={index}>
          <ListItem>
            <ListItemIcon>
              <TextsmsIcon />
            </ListItemIcon>
            <ListItemText 
              primary={truncateText(item.text)}
              secondary={
                <React.Fragment>
                  <span>{formatDate(item.timestamp)}</span>
                  <br />
                  <span>Speaker: {getSpeakerDisplayName(item.speaker)}, Ngn ng: {getLanguageDisplayName(item.language)}, Tc : {item.speed}x</span>
                </React.Fragment>
              }
            />
            <ListItemSecondaryAction>
              <IconButton
                edge="end"
                onClick={() => VoiceAPI.playAudioFile(item.result_url)}
                title="Pht audio"
              >
                <VolumeUpIcon />
              </IconButton>
              <IconButton
                edge="end"
                onClick={() => VoiceAPI.downloadResult(item.result_url)}
                title="Ti xung"
              >
                <DownloadIcon />
              </IconButton>
            </ListItemSecondaryAction>
          </ListItem>
          {index < history.length - 1 && <Divider />}
        </React.Fragment>
      ))}
    </List>
  );
};

export default TtsHistory; 